:root{
    --nightblue: #1E2055;
    --textwhite: #FFF8EE;
}

.lh{
    background-color: var(--nightblue);
    color: var(--textwhite);
    font-family: 'Inconsolata', monospace;
    height: 100%;
    width: 50%;
    position: fixed;
    overflow: hidden;
}

.lh-wrapper {
    padding: 50px;
}

.lh-pic-wrapper {
    padding: 50px;
}

.lh-pic {
    width: 500px;
    height: auto;
}

.lh-nav-wrapper {
    position: absolute;
    bottom: 20px;
    left: 20px;
    display: flex;
    flex-direction: column;
    justify-content: left;
    text-align: left;
}

.lh-subsect {
    margin: 5px 0px 0px 0px;
}


a.lh-link:link {
    color: var(--textwhite);
    background-color: transparent;
    text-decoration: none;
    }
    
    a.lh-link:visited {
    color: var(--textwhite);
    background-color: transparent;
    text-decoration: none;
    }
    
    a.lh-link:hover {
    color: var(--textwhite);
    background-color: transparent;
    text-decoration: underline;
    }
    
    a.lh-link:active {
    color: var(--textwhite);
    background-color: transparent;
    text-decoration: none;
    }

    html {
        scroll-behavior: smooth;
      }